.menu {
  z-index: 1;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  margin: 0;
  display: flex;
  opacity: 1;
  justify-content: center;
  align-items: flex-start;
}

.menu__container {
  border: 1px solid #000;
  margin: 16px 6px 0 0;
  width: 343px;
  height: 208px;
  background-color: white;
  border-radius: 10px;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menu__button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  align-self: flex-end;
  display: flex;
}

.menu__close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 14px;
  height: 14px;
}

.menu__close:hover {
  cursor: pointer;
}

.menu__link {
  text-decoration: none;
  list-style-type: none;
  grid-column: 3/3;
  grid-row: 1/1;
  display: flex;
  color: #000;
  margin: 24px 0 0 0;
}

.menu__name {
  font-family: "Cabinet Grotesk", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 25px;
  margin: 8px 12px 0 8px;
}

.menu__avatar {
  width: 40px;
  height: 40px;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 25px;
}

.menu__add {
  font-family: "Cabinet Grotesk", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  margin: 24px 0 0 0;
  border: none;
  background-color: #fff;
  padding: 0;
}

.menu__login-info {
  grid-column: 2/2;
  grid-row: 1/1;
  display: flex;
  color: #000;
}

.menu__sign {
  font-family: "Cabinet Grotesk", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 25px;
  margin: 8px 12px 0 0;
  border: none;
  background-color: #f3f3f3;
  padding: 0;
}

.menu__sign:hover {
  opacity: 0.6;
  cursor: pointer;
}

.menu__login {
  font-family: "Cabinet Grotesk", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 25px;
  margin: 8px 12px 0 8px;
  border: none;
  background-color: #f3f3f3;
  padding: 0;
}

.menu__login:hover {
  opacity: 0.8;
  cursor: pointer;
}

.menu__add:hover {
  cursor: pointer;
}

@media screen and (min-width: 375px) and (max-width: 822px) {
  .header__mobile-menu {
    display: flex;
    width: 44px;
    height: 44px;
    margin: 0 16px 0 0;
  }

  .menu__sign {
    background-color: white;
  }

  .menu__login {
    background-color: white;
  }
}

